import * as Yup from "yup";
import { useEffect, useMemo, useState } from "react";
import { useSnackbar } from "notistack";
import { useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { LoadingButton } from "@mui/lab";
import { Box, Card, Grid, Stack, Typography } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
import { useDispatch, useSelector } from "../../../redux/store";
import {
  fxRateMoncash,
  startLoadingM,
  stopLoadingM,
  makeMoncash,
  resetState,
} from "../../../redux/slices/sendmoncash";
import useIsMountedRef from "../../../hooks/useIsMountedRef";
import extractErrorMessage from "../../../utils/error";
import { useTranslation } from "react-i18next";
import {
  FormProvider,
  RHFTextField,
  RHFPhoneField,
} from "../../../components/hook-form";

export default function SendMonCashForm() {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();
  const isMountedRef = useIsMountedRef();

  const MonCashSchema = Yup.object().shape({
    phone: Yup.string().required(
      t("field.required", { obj: t("field.phone") })
    ),
    amount: Yup.number().required(
      t("field.required", { obj: t("field.amount") })
    ),
  });

  const defaultValues = useMemo(
    () => ({
      phone: "",
      amount: 0,
      rate: 0,
      amount_convert: 0,
    }),
    []
  );

  const methods = useForm({
    resolver: yupResolver(MonCashSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    setError,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  const handleAutoFxRate = async () => {
    try {
      dispatch(startLoadingM());
      const res = await fxRateMoncash(values?.amount);
      setValue("rate", res?.rate);
      setValue("amount_convert", res?.amount_convert);
      dispatch(stopLoadingM());
    } catch (err) {
      dispatch(stopLoadingM());
      const err_ = extractErrorMessage(err);
      enqueueSnackbar(err_, { variant: "error" });
    }
  };

  const onSubmit = async () => {
    if (!values?.rate) {
      enqueueSnackbar("Loading...", { variant: "info" });
      await handleAutoFxRate();
      enqueueSnackbar("Please confirm now!", { variant: "info" });
      return;
    }
    try {
      const result = await makeMoncash({
        phone: values.phone,
        amount: values.amount_convert,
      });
      reset();
      enqueueSnackbar(t("success.CREATE"));
      dispatch(resetState());
      navigate(PATH_DASHBOARD.ticket.view(result?.id));
    } catch (error) {
      const errorMessage = extractErrorMessage(error);
      enqueueSnackbar(t(`error.${errorMessage}`), { variant: "error" });
      if (isMountedRef.current) {
        if (error.response?.data?.phone) {
          setError("phone", { ...error, message: error.response?.data?.phone });
        }
      }
    } finally {
      dispatch(stopLoadingM());
      methods.clearErrors("afterSubmit");
    }
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box sx={{ display: "grid", gap: 3 }}>
              <RHFPhoneField
                name="phone"
                label={t("field.phone")}
                countryCodeEditable={false}
                onlyCountries={["ht"]}
              />
              <RHFTextField
                name="amount"
                label={`${t("field.montant")} CLP`}
                onBlur={handleAutoFxRate}
              />
              <Box
                onClick={handleAutoFxRate}
                sx={{
                  backgroundColor: "#007bff",
                  borderRadius: 0.8,
                  p: 2,
                  cursor: "pointer",
                }}
              >
                <Typography>Total:</Typography>
                <Typography
                  variant="h5"
                  sx={{ color: "white", fontWeight: "700" }}
                >
                  {values.amount_convert || 0} HTG
                </Typography>
              </Box>
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting}
              >
                {t("p_sendmoncash.sendmoncash")}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
